import Code from '@/components/Code';

export default {
    name: 'AdvancedClicks',
    components: {
        Code
    },
    data: () => ({
        width: 300,
        height: 300,
        cX: 0,
        cY: 0,
        particleType: 'star',
        trigger: true
    }),
    methods: {
        onClick(item, event) {
            this.trigger = !this.trigger;
            this.cX = event.target.offsetLeft + 25;
            this.cY = event.target.offsetTop + 25;

            switch (item) {
                case(1):
                    this.particleType = 'rect';
                    break;
                case(2):
                    this.particleType = 'circle';
                    break;
                case(3):
                    this.particleType = 'triangle';
                    break;
                case(4):
                    this.particleType = 'star';
                    break;
                case(5):
                    this.particleType = 'heart';
                    break;
            }
        }
    },
    computed: {
        code() {
            return '<template>\n' +
                '  <div class="example" :style="{\n' +
                '      width: `${width}px`,\n' +
                '      height: `${height}px`,\n' +
                '  }">\n' +
                '      <ul class="example__source-list">\n' +
                '          <li :class="`example-source example-source_${item}`"\n' +
                '              v-for="item in 5"\n' +
                '              :key="`example-source_${item}`" @click="(event) => onClick(item, event)" />\n' +
                '      </ul>\n' +
                '\n' +
                '      <VueParticlesGenerator\n' +
                '              backgroundColor="transparent"\n' +
                '              class="example__canvas"\n' +
                '              :width="width"\n' +
                '              :height="height"\n' +
                '              :particleMaxSize="40"\n' +
                '              :isLoop="false"\n' +
                '              :trigger="trigger"\n' +
                '              :particleType="particleType"\n' +
                '              position="centerCoordinates"\n' +
                '              :centerCoordinates="[cX,cY]"\n' +
                '      />\n' +
                '  </div>\n' +
                '\n' +
                '</template>\n' +
                '\n' +
                '<script>\n' +
                '    import VueParticlesGenerator from \'vue-particles-generator\';\n' +
                '\n' +
                '    export default {\n' +
                '        name: \'FollowCursor\',\n' +
                '        components: {\n' +
                '            VueParticlesGenerator\n' +
                '        },\n' +
                '        data: () => ({\n' +
                '            width: 400,\n' +
                '            height: 400,\n' +
                '            cX: 0,\n' +
                '            cY: 0,\n' +
                '            particleType: \'star\',\n' +
                '            trigger: true\n' +
                '        }),\n' +
                '        methods: {\n' +
                '            onClick(item, event) {\n' +
                '                this.trigger = !this.trigger;\n' +
                '                this.cX = event.target.offsetLeft + 25;\n' +
                '                this.cY = event.target.offsetTop + 25;\n' +
                '\n' +
                '                switch (item) {\n' +
                '                    case(1):\n' +
                '                        this.particleType = \'rect\';\n' +
                '                        break;\n' +
                '                    case(2):\n' +
                '                        this.particleType = \'circle\';\n' +
                '                        break;\n' +
                '                    case(3):\n' +
                '                        this.particleType = \'triangle\';\n' +
                '                        break;\n' +
                '                    case(4):\n' +
                '                        this.particleType = \'star\';\n' +
                '                        break;\n' +
                '                    case(5):\n' +
                '                        this.particleType = \'heart\';\n' +
                '                        break;\n' +
                '                }\n' +
                '            }\n' +
                '        }\n' +
                '    }\n' +
                '<' + '/script>\n' +
                '\n' +
                '<style lang="scss">\n' +
                '    .example {\n' +
                '        position: relative;\n' +
                '\n' +
                '        &__canvas {\n' +
                '            position: absolute;\n' +
                '            top: 0;\n' +
                '            left: 0;\n' +
                '            z-index: 1;\n' +
                '\n' +
                '            pointer-events: none;\n' +
                '        }\n' +
                '\n' +
                '        &__source-list {\n' +
                '            height: 400px;\n' +
                '            display: flex;\n' +
                '            align-items: center;\n' +
                '            justify-content: space-around;\n' +
                '            flex-wrap: wrap;\n' +
                '            list-style: none;\n' +
                '            margin: 0;\n' +
                '        }\n' +
                '    }\n' +
                '\n' +
                '    .example-source {\n' +
                '        width: 50px;\n' +
                '        height: 50px;\n' +
                '\n' +
                '        border-radius: 50%;\n' +
                '        cursor: pointer;\n' +
                '\n' +
                '        $source-colors: (1: red, 2: blue, 3: green, 4: yellow, 5: white);\n' +
                '\n' +
                '        @each $index, $color in $source-colors {\n' +
                '            &_#{$index} {\n' +
                '                background-color: $color;\n' +
                '            }\n' +
                '        }\n' +
                '\n' +
                '        &:hover {\n' +
                '            box-shadow: 0 0 5px 5px #000;\n' +
                '        }\n' +
                '    }\n' +
                '</style>'
        }
    },
}
