import Code from '@/components/Code';

export default {
    name: 'FollowCursor',
    components: {
        Code
    },
    data: () => ({
        cX: 0,
        cY: 0
    }),
    methods: {
        onMouseMove(event) {
            this.cX = event.offsetX;
            this.cY = event.offsetY;
        }
    },
    computed: {
        code() {
            return '<template>\n' +
                '    <div @mousemove="onMouseMove">\n' +
                '        <VueParticlesGenerator\n' +
                '                position="centerCoordinates"\n' +
                '                :centerCoordinates="[cX,cY]"\n' +
                '        />\n' +
                '    </div>\n' +
                '</template>\n' +
                '\n' +
                '<script>\n' +
                '    import VueParticlesGenerator from \'vue-particles-generator\';' +
                '\n' +
                '    export default {\n' +
                '        components: {\n' +
                '            VueParticlesGenerator\n' +
                '        },\n' +
                '        data: () => ({\n' +
                '            cX: 0,\n' +
                '            cY: 0\n' +
                '        }),\n' +
                '        methods: {\n' +
                '            onMouseMove(event) {\n' +
                '                this.cX = event.offsetX;\n' +
                '                this.cY = event.offsetY;\n' +
                '            }\n' +
                '        }\n' +
                '    }\n' +
                '<' + '/script>'
        }
    },
}
